.advantages__title {
  margin: 0;
  margin-bottom: 40px;
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;

  @media (--not-narrow-vp) {
    margin-bottom: 32px;
  }

  @media (--wide-vp) {
    margin-top: 71px;
    font-size: 60px;
    line-height: 60px;
  }
}

.advantages__list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  column-gap: 10px;
  row-gap: 20px;
  margin: 0 0 49px;
  padding: 0;
  list-style-type: none;
  counter-reset: number;

  @media (--not-narrow-vp) {
    column-gap: 137px;
    row-gap: 37px;
  }

  @media (--wide-vp) {
    column-gap: 73px;
    row-gap: 10px;
    margin: 0 0 69px;
  }
}

.advantages__item {
  position: relative;
  width: 274px;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 60px;
    height: 60px;
    background-color: #68b738;
  }

  @media (--not-narrow-vp) {
    width: 245px;

    &::before {
      top: 40px;
      left: 0;
      width: 80px;
      height: 80px;
    }

    &::after {
      position: absolute;
      top: 0;
      right: 0;
      font-size: 280px;
      line-height: 280px;
      color: #f2f2f2;
      z-index: -1;
      counter-increment: number;
      content: counter(number);
    }
  }
}

.advantages__description {
  margin: 0;
  padding: 0 0 0 80px;
  font-family: var(--main-font);
  font-size: 14px;
  line-height: 18px;
  color: #444444;

  @media (--not-narrow-vp) {
    padding: 151px 0 57px;
    font-size: 16px;
    line-height: 24px;
  }
}
